.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.content {
  background-color: #222;
  padding: 10px;
  color: white;
  width: 30%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.title {
  font-size: 20px;
}

.desc {
font-size: 16px;
}

.address {
    display: block;
    margin: 10px 0;
}

.info-source  {
  font-size: 16px;
  color: #909090;
  margin: 5px 0;
}

.btn {
background: #222;
width: 100%;
color: #fff;
border: 2px solid #fff;
border-radius: 25px;
padding: 5px 10px;
margin: 5px 0;
-webkit-transition-duration: 200ms;
     -o-transition-duration: 200ms;
        transition-duration: 200ms;
-webkit-transition-property: all;
-o-transition-property: all;
transition-property: all;
-webkit-transition-timing-function: ease-in-out;
     -o-transition-timing-function: ease-in-out;
        transition-timing-function: ease-in-out;
}

.btn:hover, .btn:focus {
  background: #fff;
  color: #222;
}

.cover-img {
    width: 100%;
    margin: 5px 0;
}

.items-list {
  list-style-type: none;
  padding: 0;
}


#map {
  min-height: 100vh;
  width: 70%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  .content {
    width: 100%;
     display: -ms-flexbox;
     display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
#place, .list-container {
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
}
#map {
  width: 100%;
}
}

@media screen and (max-width: 500px) {
  #place, .list-container {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
  }
}



